import React, { useState, useEffect } from 'react';
import { animated } from "react-spring";


export default function Database() {

    const [questions, setQuestions] = useState(
        [
            {
                questionText: 'What is the capital of France?',
                answerOptions: [
                    { answerText: 'New York', isCorrect: false },
                    { answerText: 'London', isCorrect: false },
                    { answerText: 'Paris', isCorrect: true },
                    { answerText: 'Dublin', isCorrect: false },
                ],
                userAnswer: '',
                per: "10%"
            },

            {
                questionText: 'The iPhone was created by which company?',
                answerOptions: [
                    { answerText: 'Apple', isCorrect: true },
                    { answerText: 'Intel', isCorrect: false },
                    { answerText: 'Amazon', isCorrect: false },
                    { answerText: 'Microsoft', isCorrect: false },
                ],
                userAnswer: '',
                per: "20%"

            },

            {
                questionText: 'The iPhone was created by which company?',
                answerOptions: [
                    { answerText: 'Motrolla', isCorrect: true },
                    { answerText: 'Intel', isCorrect: false },
                    { answerText: 'Amazon', isCorrect: false },
                    { answerText: 'Microsoft', isCorrect: false },
                ],
                userAnswer: '',
                per: "30%"


            },
            {
                questionText: 'Ref is used for referring an element or component returned by ?',
                answerOptions: [
                    { answerText: '1', isCorrect: false },
                    { answerText: '4', isCorrect: false },
                    { answerText: '6', isCorrect: false },
                    { answerText: '7', isCorrect: true },
                ],
                userAnswer: '',
                per: "40%"


            },
            {
                questionText: 'How many Harry Potter books are there?',
                answerOptions: [
                    { answerText: ' react()', isCorrect: true },
                    { answerText: 'render()', isCorrect: false },
                    { answerText: ' reduce()', isCorrect: false },
                    { answerText: ' refer()', isCorrect: false },
                ],
                userAnswer: '',
                per: "50%"

            },
            {
                questionText: 'The arbitrary inputs of components are called?',
                answerOptions: [
                    { answerText: ' Keys', isCorrect: false },
                    { answerText: 'Props', isCorrect: false },
                    { answerText: 'Elements', isCorrect: false },
                    { answerText: ' Ref', isCorrect: true },
                ],
                userAnswer: '',
                per: "60%"


            },
            {
                questionText: 'What is used to pass data to a component from outside?',
                answerOptions: [
                    { answerText: 'setState', isCorrect: false },
                    { answerText: 'render with arguments', isCorrect: false },
                    { answerText: 'PropTypes', isCorrect: false },
                    { answerText: ' props', isCorrect: true },
                ],
                userAnswer: '',
                per: "70%"


            },

            {
                questionText: 'can be done while multiple elements need to be returned from a component.?',
                answerOptions: [
                    { answerText: 'Abstraction', isCorrect: false },
                    { answerText: 'Packing', isCorrect: false },
                    { answerText: 'Insulation', isCorrect: false },
                    { answerText: 'Wrapping', isCorrect: true },
                ],
                userAnswer: '',
                per: "80%"


            },
            {
                questionText: 'React is mainly used for building ?',
                answerOptions: [
                    { answerText: 'Database', isCorrect: false },
                    { answerText: 'Connectivity', isCorrect: false },
                    { answerText: 'User interface', isCorrect: false },
                    { answerText: ' Design Platform', isCorrect: true },
                ],
                userAnswer: '',
                per: "90%"

            },
            {
                questionText: 'Who is CEO of Tesla?',
                answerOptions: [
                    { answerText: 'Jeff Bezos', isCorrect: false },
                    { answerText: 'Elon Musk', isCorrect: true },
                    { answerText: 'Bill Gates', isCorrect: false },
                    { answerText: 'Tony Stark', isCorrect: false },
                ],
                userAnswer: '',
                per: "100%"


            },
        ]
    )
    const [currentQuestion, setCurrentQuestion] = useState(0);
    const [showScore, setShowScore] = useState(false);
    const [score, setScore] = useState(0);
    const [progress, setProgress] = useState('0%')
    const handleAnswerOptionClick = (answer, question) => {
        console.log(question.per)
        setProgress(question.per)
        question.userAnswer = answer.answerText
        console.log('questions array', questions)
        setQuestions(questions)
        if (answer.isCorrect) {
            setScore(score + 1);
            
        }

        const nextQuestion = currentQuestion + 1
        if (nextQuestion < questions.length) {
            setCurrentQuestion(nextQuestion);
        } else {
            setShowScore(true);
        }
    };

    const getCorrectOption = (options) => {
        const correctOption = options.filter(item => {
            return item.isCorrect === true
        })
        console.log('asdasd', correctOption)
        return correctOption[0]
    }
    return (
        <div className='app'>
            <div className="meter">
                <animated.span style={{width: progress}}/>
            </div>
            {showScore ? (
                <>
                    <div className='score-section'>
                        You scored {score} out of {questions.length}


                    </div>
                    <div className='score-question'>
                        {
                            questions.map((item, index) => {
                                return (
                                    <div key={index}>
                                        <li>{item.questionText}</li>
                                        <h4>Your answer: <span style={{ color: "greenyellow" }}>{item.userAnswer} </span></h4>
                                        <h4>Correct answer:<span style={{ color: "greenyellow" }}> {getCorrectOption(item.answerOptions)?.answerText}</span>
                                        </h4>
                                    </div>
                                )
                            })
                        }
                    </div>
                </>
            ) : (
                <>
                    <div className='question-section'>
                        <div className='question-count'>
                            <span>Question {currentQuestion + 1}</span>/{questions.length}
                            {/* <span style={{ position: "absolute", right: "5px" }}> {seconds}</span> */}


                        </div>
                        <div className='question-text'>{questions[currentQuestion].questionText}</div>
                    </div>
                    <div className='answer-section'>
                        {questions[currentQuestion].answerOptions.map((answerOption, answeIndex) => (
                            <button key={answeIndex} onClick={() => handleAnswerOptionClick(answerOption, questions[currentQuestion])}>{answerOption.answerText}</button>

                        ))

                        }
                    </div>
                </>
            )}
        </div>
    );
}
