import React, { useState } from 'react';
import firebase from "firebase"
import { TextField } from '@material-ui/core';
import {
    Link,
    useHistory
} from "react-router-dom";

export default function Signin() {
    let history = useHistory();


    function handleClick2() {
        history.push("/Database");

    }

    function handleClick3() {
        history.push("/");
    }

    const [email, setEmail] = useState('');

    const bindEmail = (e) => {
        console.log('value', e.target.value)
        setEmail(e.target.value)
    };



    const [password, setPassword] = useState('');

    const bindPassword = (e) => {
        console.log('value', e.target.value)
        setPassword(e.target.value)
    };


    const register = (e) => {

        console.log('email', email)
        console.log('password', password)

        firebase.auth().signInWithEmailAndPassword(email, password)
            .then((userCredential) => {
                // Signed in 
                var user = userCredential.user;

                console.log("auth done")
                // ...
            })
            .catch((error) => {
                var errorCode = error.code;
                var errorMessage = error.message;
                // ..
            });

        e.preventDefault();

    }

    return (
        <form className="auth-form" autoComplete="off">
            <h1 style={{color:"#2196f3"}}>Sign-in form</h1>

                
            <TextField 
            id="email" 
            label="Enter you email" 
            variant="outlined" 
            onChange={bindEmail} 
            />

<TextField 
            label="Enter password" 
            variant="outlined"  
            id="password"
            onChange={bindPassword}
                   
            />
     
   
            <p className="redirect">Don't have account 
            
            <button type="submit" className="redirect-button" onClick={handleClick3}>Sign-up</button></p>
            <button type="submit" className="register-button" onClick={register} onClick={handleClick2}>Register</button>
        </form>
    )

}
