import React, { useState } from 'react';
import "../index.css"
import firebase from "firebase"
import { TextField } from '@material-ui/core';

import {
    Link,
    useHistory
} from "react-router-dom";

export default function Signup() {
    let history = useHistory();


    function handleClick2() {
        history.push("/Database");
    }


    function handleClick3() {
        history.push("/Signin");
    }

    const [email, setEmail] = useState('');

    const bindEmail = (e) => {
        console.log('value', e.target.value)
        setEmail(e.target.value)
    };

    const [name, setName] = useState('');

    const bindName = (e) => {
        console.log('value', e.target.value)
        setName(e.target.value)
    };

    const [password, setPassword] = useState('');

    const bindPassword = (e) => {
        console.log('value', e.target.value)
        setPassword(e.target.value)
    };


    const register = (e) => {

        console.log('email', email)
        console.log('password', password)

        firebase.auth().createUserWithEmailAndPassword(email, password)
            .then((userCredential) => {
                // Signed in 
                var user = userCredential.user;
                handleClick2()
                console.log("auth done")

                firebase.firestore().collection(`users`).doc(user.uid).set({
                    name,
                    email,
                    userUID: user.uid
                })
                    .then(() => {
                        console.log("Document successfully written!");
                    })
                    .catch((error) => {
                        console.error("Error writing document: ", error);
                    });

            }).catch((error) => {
                var errorCode = error.code;
                var errorMessage = error.message;
                // ..
            });


            setEmail('');
            setName('');
            setPassword('')
        e.preventDefault();

    }

    return (
        <form className="auth-form" autoComplete="off">
            <h1 style={{ color: "#2196f3" }}>Sign-up form</h1>
            <TextField
                type="name"
                name="name"
                id="name"
                label="Enter you name"
                value={name}
                onChange={bindName}

            />

            <TextField
                id="email"
                label="Enter you email"
                variant="outlined"
                value={email}
                onChange={bindEmail}
            />


            <lTextField
                label="Enter your age"
                variant="outlined"
                name="age"
                id="age"

            />

            <TextField
                label="Enter password"
                variant="outlined"
                onChange={bindPassword}
                id="password"
                type="password"
                value={password}

            />
            <p className="redirect">Already have account <button type="submit" className="redirect-button" onClick={handleClick3}>Sign-in</button></p>

            <button type="submit" className="register-button" onClick={register} >Register</button>


        </form>
    )

}

